import type { AuthenticationData, LoginAuthenticationData, UpdateAuthenticationData } from '@strife/common';
import { AuthenticationSchema } from '@strife/common';
import { injectable } from 'inversify';
import di from '../di';
import { UserServiceId, type IUser } from '../user.interface';
import type { IAuthenticationService } from './authentication.service.interface';
import { AuthenticationServiceServiceId } from './authentication.service.interface';
import BaseService from './base.service';

@injectable()
class AuthenticationService extends BaseService implements IAuthenticationService {

  protected override get baseUrl(): string | URL | undefined {
    return `http://localhost:3000/authentication`;
  }

  public async login(data: LoginAuthenticationData): Promise<AuthenticationData> {
    if (this.user.authentication)
      return Promise.reject();
    
    const authenticationData = await this.post<AuthenticationData>({ schema: AuthenticationSchema, data });
    this.user.login(authenticationData);
    return authenticationData;
  }

  public async logout(): Promise<void> {
    if (!this.user.authentication)
      return Promise.reject();

    await this.delete({ url: `/${this.user.authentication.account_id}` });
    this.user.logout();
  }

  public async updateAuthentication(data: UpdateAuthenticationData): Promise<AuthenticationData> {
    const userService = di.get(UserServiceId);
    if (!userService.authentication)
      return Promise.reject();

    return this.put<AuthenticationData>({ schema: AuthenticationSchema, url: `/${userService.authentication.account_id}`, data });
  }

}

di.bind(AuthenticationServiceServiceId).to(AuthenticationService).inSingletonScope();