import { SnackbarProps } from '@mui/material/Snackbar';
import { type IAction } from '@strife/common';
import { ServiceIdentifier } from "inversify";

export interface IToasts {
  active?: IToastData;
  onChange: IAction<[]>;
  add(data: IToastData): IToastHandle;
  remove(handle: IToastHandle): void;
}

export interface IToastData extends SnackbarProps {}

export interface IToastHandle {
  dismiss(): void;
}

export const ToastsServiceId: ServiceIdentifier<IToasts> = Symbol.for('ToastsServiceId');