'use client'

import Snackbar from '@mui/material/Snackbar';
import { Fragment, JSX, useEffect, useState } from 'react';
import di from '../src/di';
import '../src/toasts';
import { type IToastData, ToastsServiceId } from '../src/toasts.interface';

export default function Toasts(): JSX.Element {
  const toasts = di.get(ToastsServiceId);
  const [ snackbar, setSnackbar ] = useState<JSX.Element | undefined>();
  useEffect(initialize, []);

  return <Fragment>{snackbar}</Fragment>;

  function onChange(): void {
    const snackbar = generate(toasts.active);
    setSnackbar(snackbar);
  }

  function initialize(): void {
    onChange();
    toasts.onChange.add(onChange);
    const handle = toasts.add({ message: 'Hello, world!', autoHideDuration: 2000 });
    const handle2 = toasts.add({ message: 'Goodbye, world.', autoHideDuration: 2000 });
  }

  function generate(data?: IToastData): JSX.Element {
    console.log(data)
    return data
      ? <Snackbar {...data} />
      : <Fragment />;
  }
}
